import moment from 'moment';
import { React, useState } from 'react';
import {
  Typography,
  Image,
  Form,
  Input,
  DatePicker,
  Button,
} from 'antd';
import { FileDoneOutlined } from '@ant-design/icons';

function range(begin, end) {
  const result = [];
  for (let i = begin; i < end; ++i) {
    result.push(i);
  }
  return result;
}

function Enlist() {
  const [formData, setFormData] = useState();
  function onFinish(values) {
    console.log('onFinish', values);
    setFormData(values);
  }
  return (
    <>
      <Typography.Title>  </Typography.Title>
      <Typography.Paragraph>
                  !
         , trust me.
      </Typography.Paragraph>
      <Form
          layout="horizontal"
          labelCol={{span: 3}}
          // DatePicker      
          //    ,   ,  
          // .        
          //  .
          initialValues={{datetime: moment().add(1, 'hour').startOf('hour')}}
          onFinish={onFinish}>
        <Form.Item
            name="name" label=" "
            rules={[{
              required: true,
              message: (
                <Image width={500}
                  src="https://memepedia.ru/wp-content/uploads/2020/01/hto-ja.png" />
              )
            }]}>
          <Input placeholder="   !" />
        </Form.Item>
        <Form.Item
            name="datetime"
            label=""
            rules={[{
              required: true,
              message: ' ,    , ,   ...'
              // DatePicker      ,   
            }]}>
          <DatePicker
              style={{width: "100%"}}
              renderExtraFooter={() => <> -  !</>}
              disabledDate={
                (date) => date < moment()
                       || date.minute() % 30 !== 0
                       || date.second() !== 0
              }
              showNow={false}
              showTime={{
                disabledMinutes: () => range(1, 30).concat(range(31, 60)),
                disabledSeconds: () => range(1, 60),
                hideDisabledOptions: true,
                showSecond: false,
              }}
              />
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit"></Button>
        </Form.Item>
        {formData &&
          <Typography.Paragraph>
            , {formData.name},   
             {formData.datetime.format()}!
          </Typography.Paragraph>
        }
      </Form>
    </>
  );
}

const enlistRoute = {
  title: '',
  menuKey: 'enlist',
  route: '/enlist',
  Icon: FileDoneOutlined,
  Content: Enlist,
};

export default enlistRoute;
