import { useEffect, useState } from 'react';
import {
  Typography,
  Image,
  Form,
  Input,
  Button,
  Card,
  notification,
} from 'antd';
import {
  UserOutlined,
} from '@ant-design/icons';
import { Octokit } from '@octokit/rest';

//    :)
const octokit = new Octokit();

function useGithubData(username) {
  const [data, setData] = useState();
  useEffect(() => {
    octokit.rest.users.getByUsername({username})
      .then((res) => {
        console.log(res.data);
        setData({
          profileLink: res.data.html_url,
          username: res.data.login,
          name: res.data.name,
          avatarUrl: res.data.avatar_url,
          bio: res.data.bio,
        });
      })
      .catch((err) => {
        notification.open({
          message: '',
          description: '    GitHub ',
        });
        console.error(err);
      });
  }, [username]);
  return data;
}

function About() {
  const [username, setUsername] = useState('nic11');
  const ghData = useGithubData(username);
  return (
    <div>
      <Typography.Title> </Typography.Title>
      <Typography.Paragraph>
           ,  :
        <ul>
          <li> </li>
          <li>    </li>
          <li>    </li>
        </ul>
      </Typography.Paragraph>
      <Typography.Paragraph>
         : {
          ghData?
            (
              <Typography.Link href={ghData.profileLink} target="_blank">
                {ghData.profileLink}
              </Typography.Link>
            )
          :
            '...'
        }
      </Typography.Paragraph>
      {ghData &&
        (
          <Card
              style={{width: 250}}
              cover={<Image src={ghData.avatarUrl} />}
          >
            <Card.Meta
                title={ghData.name || ghData.username}
                description={<>Bio: {ghData.bio || ' '}</>}
              />
          </Card>
        )
      }
      <Form
          layout="inline"
          initialValues={{username: 'nic11'}}
          onFinish={(data) => setUsername(data.username)}>
        <Form.Item name="username">
          <Input placeholder="" />
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit"></Button>
        </Form.Item>
      </Form>
    </div>
  );
}

const aboutRoute = {
  title: ' ',
  menuKey: 'about',
  route: '/about',
  Icon: UserOutlined,
  Content: About,
};

export default aboutRoute;
